<template>
  <div>
    <HeroSection class="reveal" />
    <FeaturesSection class="reveal" />
    <UserTypeSelector class="reveal" />
    <TestimonialsSection class="reveal" />
    <ContactForm class="reveal" />
    <AppFooter />
  </div>
</template>

<script>
import HeroSection from '@/components/HeroSection.vue';
import FeaturesSection from '@/components/FeaturesSection.vue';
import TestimonialsSection from '@/components/TestimonialsSection.vue';
import ContactForm from '@/components/ContactForm.vue';
import UserTypeSelector from '@/components/UserTypeSelector.vue';
import AppFooter from '@/components/AppFooter.vue';

export default {
  components: {
    HeroSection,
    FeaturesSection,
    UserTypeSelector,
    TestimonialsSection,
    ContactForm,
    AppFooter
  },

  mounted() {
    // Create an Intersection Observer
    const options = {
      root: null, // Use the viewport as the root
      threshold: 0.1 // Trigger when 10% of the element is visible
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach(entry => {
        if (entry.isIntersecting) {
          entry.target.classList.add('visible'); // Add class to reveal the element
          observer.unobserve(entry.target); // Stop observing once revealed
        }
      });
    }, options);

    // Observe each section with the class 'reveal'
    const reveals = document.querySelectorAll('.reveal');
    reveals.forEach((reveal) => {
      observer.observe(reveal);
    });
  },


};
</script>

<style scoped>
/* Scoped styles for Home.vue */
/* Initial hidden state */
.reveal {
  opacity: 0;
  transform: translateY(50px); /* Start slightly below */
  transition: opacity 0.6s ease, transform 0.6s ease; /* Transition for smooth reveal */
}

/* Class to show the element */
.reveal.visible {
  opacity: 1;
  transform: translateY(0); /* Move to original position */
}


/* Adjust for different screen sizes */
@media (max-width: 768px) {
  .container {
    padding: 10px; /* Add padding for smaller screens */
  }

  section {
    padding: 15px; /* Adjust padding for sections */
  }
}
</style>
