<template>
    <div class="user-type-selector">
      <h2>What kind of user are you?</h2>
  
      <div class="options">
        <button @click="selectUserType('learningProvider')">Learning Provider</button>
        <button @click="selectUserType('learner')">Learner</button>
      </div>
  
      <!-- Learning Provider Option with WhatsApp Form -->
      <div v-if="userType === 'learningProvider'" class="provider-option">
        <h3>Request a Demo of Our LMS</h3>
        <p>As a learning provider, you can request a demo to see how our LMS works.</p>
  
        <div class="whatsapp-form">
          <label>
            Name:
            <input v-model="demoRequest.name" type="text" placeholder="Your Name" required />
          </label>
  
          <label>
            Phone Number:
            <div class="phone-input">
              <select v-model="demoRequest.countryCode">
                <option v-for="(code, country) in countryCodes" :key="country" :value="code">
                  {{ country }} ({{ code }})
                </option>
              </select>
              <input v-model="demoRequest.phone" type="tel" placeholder="Phone Number" required />
            </div>
          </label>
  
          <label>
            Message:
            <textarea v-model="demoRequest.message" placeholder="Your Message" required></textarea>
          </label>
  
          <button @click="sendWhatsAppMessage">Send via WhatsApp</button>
        </div>
      </div>
  
      <!-- Learner Option with Provider Cards -->
      <div v-if="userType === 'learner'" class="learner-option">
        <h3>Available Courses from Our Learning Providers</h3>
        <div class="provider-cards">
          <div v-for="provider in providers" :key="provider.id" class="card" @click="openModal(provider)">
            <img :src="provider.logo" alt="Provider Logo" />
            <h4>{{ provider.name }}</h4>
            <p class="category">{{ provider.category }}</p> <!-- Display the provider category -->
            <!-- Show More Button with Link to Provider's Course Page -->
            <a :href="provider.link" target="_blank" class="show-more">Courses</a>
          </div>
        </div>
      </div>
  
      <!-- Modal for Course List -->
      <div v-if="showModal" class="modal-overlay" @click="closeModal">
        <div class="modal-content" @click.stop>
          <h3>Courses Offered by {{ selectedProvider.name }}</h3>
          <ul>
            <li v-for="course in selectedProvider.courses" :key="course.id">{{ course.name }}</li>
          </ul>
          <button @click="closeModal">Close</button>
        </div>
      </div>
    </div>
