    // Validate that name, phone, and message are provided
    if (!name || !countryCode || !phone || !message) {
        alert('Please fill in all required fields: Name, Phone, and Message.');
        return;
    }

    const fullPhone = `${countryCode}${phone}`;
    const targetPhone = '639665385452'; // Fixed target number

